<template>
  <div class="login_container bgImg d-flex jc-center ai-center">
    <div class="login_box d-flex flex-column jc-center">
      <div class="login_title">
        <div class="title1">321Design</div>
        <div class="title2">登录</div>
      </div>
      <el-form
        class="login_form"
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        @submit.native.prevent="submitForm('ruleForm')"
      >
        <el-form-item prop="username">
          <el-input
            v-model="ruleForm.username"
            placeholder="请输入用户名"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            type="password"
            v-model="ruleForm.password"
            placeholder="请输入密码"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="loginBtn" type="primary" native-type="submit"
            >登录</el-button
          >
        </el-form-item>
      </el-form>
      <p style="margin:0 auto;">321Design CopyRight ©2018-2020</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {},
      rules: {
        username: [
          { required: true, message: "请输入账号", trigger: "change" },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "change" },
        ],
      },
    };
  },
  methods: {
    //  登录
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const res = await this.api
            .merchantsUserIndex(this.ruleForm)
            .catch((err) => {
              if (err.data.code == 201) {
                this.errorMess(err.data.data);
              }
            });
          if (res) {
            this.$store.commit("CHANGE_LOGIN_STATUS", "logged");
            this.$store.commit("CHANGE_USER_NAME", this.ruleForm.username);
            this.$store.commit("CHANGE_USER_ID", res.data.data.id);
            this.$store.commit("CHNAGE_TOKEN", res.data.data.token);
            this.successMess("登录成功");
            this.$router.push("/");
          }
        } else {
          return false;
        }
      });
    },
  },
  created() {
    if (sessionStorage.getItem("loginStatus") == "logged") {
      this.$router.push("/");
    }
  },
};
</script>

