import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import VueLazyLoad from 'vue-lazyload'
import { postRequest,getRequest,imgRequest,postLogisticsData } from './utils/api';
import { api } from './utils/requestApi'
import { successMess, warningMess, errorMess } from './utils/message.js'
import VCharts from 'v-charts'

import '../src/assets/scss/public.scss'
import './plugins/element.js'

Vue.use(VCharts);
Vue.use(VueLazyLoad, {
    preLoad: 1.3,
    attempt: 1
})
Vue.config.productionTip = false
Vue.prototype.postRequest = postRequest;
Vue.prototype.getRequest = getRequest;
Vue.prototype.imgRequest = imgRequest;
Vue.prototype.postLogisticsData = postLogisticsData;
Vue.prototype.api = api;
Vue.prototype.successMess = successMess;
Vue.prototype.warningMess = warningMess;
Vue.prototype.errorMess = errorMess;

router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) {
      if (sessionStorage.getItem('loginStatus') == 'logged' && sessionStorage.getItem('userName') != '' && sessionStorage.getItem('userId') != '' && sessionStorage.getItem('token') != '') {
          if (sessionStorage.getItem('userName') != 'undefined' && sessionStorage.getItem('userId') != 'undefined') {
              next();
          } else {
              next({
                  path: '/Login',
                  query: { redirect: to.fullPath }
              })
          }
      } else {
          next({
              path: '/Login',
              query: { redirect: to.fullPath }
          })
      }
  } else {
      next();
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
