<template>
  <div class="HomeMainCom">
    <div class="d-flex">
      <div class="card chart" v-show="containerState2">
        <div class="title">我的订单走势</div>
        <div v-show="collapseState2">
          <el-row class="chart_btn">
            <el-button
              style="background: #f74041;color: #fff;"
              icon="iconfont icon_zhexiantu"
              circle
              size="mini"
              @click="clickChartButton(0)"
            ></el-button>
            <el-button
              style="background: #f74041;color: #fff;"
              icon="iconfont icon_zhuzhuangtu"
              circle
              size="mini"
              @click="clickChartButton(1)"
            ></el-button>
            <el-button
              style="background: #f74041;color: #fff;"
              icon="iconfont icon_bingzhuangtu"
              circle
              size="mini"
              @click="clickChartButton(2)"
            ></el-button>
          </el-row>
          <div v-if="chartState[0]">
            <ve-line :data="chartData" height="370px"></ve-line>
          </div>
          <div v-if="chartState[1]">
            <ve-histogram :data="chartData" height="370px"></ve-histogram>
          </div>
          <div v-if="chartState[2]">
            <ve-pie :data="chartData" height="370px"></ve-pie>
          </div>
        </div>
      </div>
      <div class="card news">
        <div class="title">动态早知道</div>
        <ul class="news_lists">
          <li v-for="(item, index) in noticeList" :key="index">
            <span>[通知]</span>
            <span class="color3b98c8" @click="clickNotice(item)">{{ item.title }}</span>
            <span class="color3b98c8">{{ item.create_at.split(' ')[0] }}</span>
          </li>
        </ul>
      </div>
      <div class="d-flex flex-column jc-between ai-center" style="width:35%;min-width:380px">
        <div class="userInfo card">
          <div class="title">个人中心</div>
          <div class="avator">
            <el-avatar
              :size="80"
              src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
            ></el-avatar>
            <span>userName</span>
          </div>
        </div>
        <div class="card note">
          <div class="title note_title">通知：{{ divOneCon.title }}</div>
          <div class="title_main">
            <div class="note_reference">{{ divOneCon.reference }}</div>
            <div class="note_content">{{ divOneCon.content }}</div>
            <div class="note_author">—— {{ divOneCon.author }} ——</div>
            <div class="note_time">发布于：{{ divOneCon.create_at || ''.slice(0,16) }}</div>
          </div>
        </div>
      </div>
    </div>
      <div class="card table">
        <div class="title">产品上新</div>
        <div class="table_form">
            <el-table :data="tableData" stripe height="54vh" style="width: 100%;">
              <el-table-column label="图片" width="50px">
                <template slot-scope="scope">
                  <img v-lazy="scope.row.imgs" alt srcset class="shopImgCon" />
                </template>
              </el-table-column>
              <el-table-column prop="name" label="品名"></el-table-column>
              <el-table-column prop="engName" label="英文名"></el-table-column>
              <el-table-column prop="create_at" label="加入时间"></el-table-column>
            </el-table></div>
      </div>
    <!-- 通知弹出框 -->
    <el-dialog
      :title="`${dialogCon.title} ${dialogCon.create_at}`"
      :visible.sync="centerDialogVisible"
      width="50%"
      center
    >
      <p>{{ dialogCon.reference }}</p>
      <p class="dialogContent">{{ dialogCon.content }}</p>
      <span slot="footer" class="dialog-footer"></span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      icon0: 'el-icon-arrow-down',
      icon1: 'el-icon-arrow-down',
      icon2: 'el-icon-arrow-down',
      tableData: [], //新产品
      chartData: {
        columns: ['第几周', '下单量'],
        rows: [],
      },
      noticeList: [], //最新动态
      containerState0: true, //父容器div显示状态
      containerState1: true, //父容器div显示状态
      containerState2: true, //父容器div显示状态
      collapseState0: true, //折叠面板显示状态
      collapseState1: true, //折叠面板显示状态
      collapseState2: true, //折叠面板显示状态
      chartState: [true, false, false], //图表显示状态
      centerDialogVisible: false, //通知弹出框显示状态
      dialogCon: {}, //弹出框主体信息
      divOneCon: {}, //通知信息
    }
  },
  methods: {
    //折叠面板显示状态+更换图标
    clickIconOne(iconVal, index) {
      if (iconVal == 'el-icon-arrow-up' && index == 0) {
        this.collapseState0 = true
        this.icon0 = 'el-icon-arrow-down'
      }
      if (iconVal == 'el-icon-arrow-down' && index == 0) {
        this.collapseState0 = false
        this.icon0 = 'el-icon-arrow-up'
      }
      if (iconVal == 'el-icon-arrow-up' && index == 1) {
        this.collapseState1 = true
        this.icon1 = 'el-icon-arrow-down'
      }
      if (iconVal == 'el-icon-arrow-down' && index == 1) {
        this.collapseState1 = false
        this.icon1 = 'el-icon-arrow-up'
      }
      if (iconVal == 'el-icon-arrow-up' && index == 2) {
        this.collapseState2 = true
        this.icon2 = 'el-icon-arrow-down'
      }
      if (iconVal == 'el-icon-arrow-down' && index == 2) {
        this.collapseState2 = false
        this.icon2 = 'el-icon-arrow-up'
      }
    },
    //点击容器close
    clickIconTwo(index) {
      if (index == 0) {
        this.containerState0 = false
      } else if (index == 1) {
        this.containerState1 = false
      } else {
        this.containerState2 = false
      }
    },
    //更改图标显示状态
    clickChartButton(val) {
      this.chartState = []
      for (let i = 0; i < 3; i++) {
        this.chartState.push(false)
      }
      this.chartState[val] = true
    },
    //点击通知弹出通知框
    clickNotice(item) {
      this.dialogCon = item
      this.centerDialogVisible = true
    },
    initData() {
      this.postRequest('/v1/system').then((res) => {
        console.log(res)
        this.noticeList = res.data.data.dynamics
        //  this.divOneCon = this.noticeList[0];
        if (this.noticeList.length !== 0) {
          this.divOneCon = this.noticeList[0]
        } else {
          this.divOneCon = this.noticeList
        }
        this.tableData = res.data.data.goods
        for (const key in res.data.data.order_num) {
          if (res.data.data.order_num.hasOwnProperty(key)) {
            const element = res.data.data.order_num[key]
            this.chartData.rows.push({ 第几周: `第${key}周`, 下单量: element })
          }
        }
      })
    },
  },
  beforeMount() {
    this.$nextTick(function () {
      this.initData()
    })
  },
}
</script>

