import axios from 'axios';
import { Message } from 'element-ui';
// import apiConfig from '../../config/api.config'
axios.defaults.baseURL = 'http://api.321.design'
import judgeErrorCode from './errorCode.js'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style
NProgress.configure({
    showSpinner: false
})

// axios.defaults.baseURL = 'http://api.321.design'

axios.interceptors.request.use(
    config => {
        NProgress.start();
        let defaultParams = {
            u_id: sessionStorage.getItem('selectUId'),
        };
        // // 默认值与前面传来的参数进行合并
        // config.data = Object.assign(defaultParams, config.data);
        if (config.headers['Content-Type'] != 'multipart/form-data') {
            config.data = Object.assign(defaultParams, config.data);
        }
        return config;
    },
    error => {
        Message.error({ message: '请求超时！' });
        return Promise.resolve(error);
    });
//接口文档出来之后再写
axios.interceptors.response.use(function(res) {

    // 对响应数据做点什么
    // 这里的状态码可根据实际情况来修改
    if (res.data.code == '200') {
        NProgress.done();
        return Promise.resolve(res);
    } else {
        // judgeErrorCode详情看errorCode文件
        judgeErrorCode(`code${res.data.code}`, res.data.msg)
        return Promise.reject(res);
    }
}, function(error) {
    // 对响应错误做点什么
    judgeErrorCode(`code${500}`, '未知错误');
    return Promise.reject(error);
});

export const postRequest = (url, params, headers = { 'Content-Type': 'application/x-www-form-urlencoded' }) => {
    return axios({
        method: 'post',
        url,
        data: params,
        transformRequest: [function(data) {
            let req = '';
            for (const key in data) {
                req += encodeURIComponent(key) + '=' + encodeURIComponent(data[key]) + '&'
            }
            return req;
        }],
        headers,
    });
};

export const getRequest = (url, params, headers = { 'Content-Type': 'application/x-www-form-urlencoded' }) => {
    return axios({
        method: 'get',
        url,
        data: params,
        transformRequest: [function(data) {
            let req = '';
            for (const key in data) {
                req += encodeURIComponent(key) + '=' + encodeURIComponent(data[key]) + '&'
            }
            return req;
        }],
        headers,
    });
}
