export const getters ={
    getCateId: state => {
        return state.cate.cate_id;
      },
       //返回图库左边id
       getImgListCateId: state => {
        return sessionStorage.getItem('oldShopCate_id');
      },
      //返回商品管理目录id
      getShopCateId: state => {
        return state.shopManagementCate.cate_id;
      },
      card: state => { return state.cardType === 1 ? state.frontCard : state.behindCard },
      htmlFontSize: state => state.htmlFontSize,
      cardType: state => state.cardType,
      frontCard: state => state.frontCard,
      selectedObj: state => state.selectedObj,
      canvasState: state => state.canvasState,
      undoList: state => state.undoList,
      redoList: state => state.redoList,
      previewImg: state => state.previewImg,
      cardList: state => state.cardList,
      desginCanvasIndex: state => state.desginCanvasIndex,
      canvasList: state => state.canvasList,
      // 2019.4.19
      canvasProduct: state => state.canvasProduct,
}