      </el-submenu>
    </el-menu>

    <el-dialog title="修改密码" :visible.sync="dialogFormVisible" width="550px" class="editPsdDialog">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" size="small">
        <el-form-item label="原始密码" prop="oldPass" class="marginBottm5">
          <el-input
            v-model="ruleForm.oldPass"
            placeholder="请输入原始密码"
            style="width: 400px!important;"
          ></el-input>
        </el-form-item>
        <el-form-item label="新密码" prop="pass" class="marginBottm5">
          <el-input
            v-model="ruleForm.pass"
            placeholder="请输入新密码"
            style="width: 400px!important;margin-left: 15px;"
          ></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPass" class="marginBottm5">
          <el-input
            v-model="ruleForm.checkPass"
            placeholder="请再次输入密码"
            style="width: 400px!important;"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogFormVisible = false">取 消</el-button>
        <el-button size="small" type="primary" @click="submitForm('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
