<script>
import logo321 from './321Logo';
export default {
  components:{
    logo321
  },
  data() {
    let validateOldPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        callback()
      }
    }
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入新密码'))
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass')
        }
        callback()
      }
    }
    let validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入新密码'))
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      galleryName: '图库',
      orderActive: '提交订单',
      orderManageActive: '订单管理',
      numActive: '商品管理',
      dataActive: '数据分析',
      fbaActive: 'fba',
      // activeIndex: '',
      userName: '',
      dialogFormVisible: false,
      ruleForm: {
        pass: '',
        checkPass: '',
        oldPass: '',
      },
      rules: {
        pass: [{ validator: validatePass, trigger: 'blur' }],
        checkPass: [{ validator: validatePass2, trigger: 'blur' }],
        oldPass: [{ validator: validateOldPass, trigger: 'blur' }],
      },
    }
  },

  methods: {
    // 点击订单子菜单
    handleClickMenu(value) {
      this.orderActive = value
    },
    // 点击订单子菜单
    handleClickOrderManageMenu(value) {
      this.orderManageActive = value
    },
    // 点击图库子菜单
    clickGalleryActive(value) {
      this.galleryName = value
    },
    //点击fba
    clickfbaActive(value) {
      this.fbaActive = value
    },
    // 点击账号子菜单
    clickNumActive(value) {
      this.numActive = value
    },
    // 点击数据分析
    clickDataActive(value) {
      this.dataActive = value
    },
    // 激活路由
    handleSelect(index, path) {
      sessionStorage.setItem('activeNav', index)
      if (index == '/imageList') {
        sessionStorage.setItem('imageType', 1)
        this.$store.commit('CHANGE_IMGLIST_CATE_IMAGE_TYPE', 1)
      }
      if (index == '/myImageList') {
        sessionStorage.setItem('imageType', 2)
        this.$store.commit('CHANGE_IMGLIST_CATE_IMAGE_TYPE', 2)
      }
      if (index == '/openedImageList') {
        sessionStorage.setItem('imageType', 3)
        this.$store.commit('CHANGE_IMGLIST_CATE_IMAGE_TYPE', 3)
      }
    },
    //修改密码对话框显示状态及操作
    changePass() {
      this.dialogFormVisible = true
    },
    //修改密码
    changePassWordFun(
      old_pwd,
      new_pwd,
      u_id = sessionStorage.getItem('selectUId')
    ) {
      this.postRequest(
        `/v1/merchantsUserPwdEdit?old_pwd=${old_pwd}&new_pwd=${new_pwd}&u_id=${u_id}`
      ).then((res) => {
        this.dialogFormVisible = false
        if (res.data.code == '200') {
          this.$message({
            type: 'success',
            message: '修改成功',
          })
        }
        if (res.data.code == '202') {
          this.$message.error('原始密码错误')
        }
      })
    },
    //提交前检测
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.changePassWordFun(this.ruleForm.oldPass, this.ruleForm.pass)
        } else {
          return false
        }
      })
    },
    //退出系统
    logoutSysyem() {
      this.$confirm('此操作将退出系统, 是否继续?', '退出', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          this.signOutFun()
          this.$message({
            type: 'success',
            message: '退出成功!',
          })
        })
    },
    signOutFun() {
      sessionStorage.removeItem('loginStatus')
      sessionStorage.removeItem('userName')
      sessionStorage.removeItem('userId')
      sessionStorage.removeItem('selectUId')
      sessionStorage.removeItem('token')
      this.$router.push('/Login')
    },
  },
  created() {
    this.userName = sessionStorage.getItem('userName')
  },
}
</script>
